const {ccclass, property} = cc._decorator;

type test = { maxNum: number };

@ccclass
export default class TweenTest extends cc.Component {

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}


    @property(cc.Label)
    numLab: cc.Label = null;

    @property(cc.Node)
    testNode: cc.Node = null;

    testNum: test = {
        maxNum: 1000
    }

    startCountDown () {
        if (this.testNum.maxNum != 0) {
            cc.tween(this.testNum)
                .to(30, { maxNum: 0 }, { easing: 'cubicOut'})
                .start()
        }
    }

    startNodeMove () {
        cc.tween(this.testNode)
            .set({ position: cc.v3(-400, 0, 0) })
            .to(3, { position: cc.v3(400, 0, 0) }, { easing: 'cubicOut'})
            .start()
    }

    update () {
        if (this.testNum.maxNum >= 0) {
            this.numLab.string = Math.floor(this.testNum.maxNum).toString();
        }
    }

    // update (dt) {}
}
